}

.bc {
    width:100%;
    margin:0px;
    padding: 17px 50px;
    border-radius:0px;
}

.bc:hover {
    background-image:linear-gradient(rgb(16, 186, 243),rgb(45, 45, 138));
    color:white;
}

.bc:active {
    transform:translateY(0px);
}
